<script>
import { GetCoupon, ExchangeCoupon } from "../api/user.js";

export default {
  components: {},
  data() {
    return {
      loading: false,
      finished: false,
      list: [],
      page: 0,
      info: [],
      text: "",
      des: "",
    };
  },
  computed: {},
  methods: {
    onLoad() {
      this.page++;
      GetCoupon({ page: this.page }).then((response) => {
        this.info = response.info;
        this.text = response.text;
        this.des = response.des;

        this.list = [];
        response.List.forEach((item) => {
          item.show = false;

          this.list.push(item);
        });
        this.loading = false;
        if (response.TotalPage <= this.page) {
          this.finished = true;
        }
      });
    },
    onShowInfo(id, index) {
      this.list.forEach((item, itemIndex) => {
        if (index == itemIndex) {
          item.show = !item.show;
          return;
        }
      });
    },
    onLoadUse() {
      this.usePage++;
      GetCoupon({ page: this.usePage }).then((response) => {
        response.List.forEach((item) => {
          this.useList.push(item);
        });
        this.useLoading = false;
        if (response.TotalPage <= this.usePage) {
          this.useFinished = true;
        }
      });
    },
    onLoadEnd() {
      this.endPage++;
      GetCoupon({ page: this.endPage }).then((response) => {
        response.List.forEach((item) => {
          this.endList.push(item);
        });
        this.endLoading = false;
        if (response.TotalPage <= this.endPage) {
          this.endFinished = true;
        }
      });
    },

    onClick(index, subindex, lineindex) {
      // console.log(  index,subindex,lineindex )
      let flag =
        this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"];
      this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"] = !flag;
    },
    goPage(pagepath){

      this.$router.push({ path: pagepath})
    }
  },
};
</script>

